// Sidebar.js
import React, { useContext } from 'react';
import "./SideBar.css";
import { useState } from 'react';
import { Link } from 'react-scroll';
import Toggle from '../Toggle/Toggle';
import { themeContext } from "../../Context";


function Sidebar() {
    const theme = useContext(themeContext);
    const darkMode = theme.state.darkMode;
    const [isVisible, setIsVisible] = useState(false);

    const toggleVisibility = () => {
        setIsVisible(!isVisible);
    };
    return (
        <>
            <div className="sideToggle" >
                <span onClick={toggleVisibility}><i className="fa fa-bars"></i></span>
                {isVisible ? (
                    <div className="sidebar ">
                        <div >
                            <p><i onClick={toggleVisibility} className='fa fa-xmark'></i></p>
                           <div className="n-name"> {darkMode ? <div >Light</div> :
                                <div >Dark</div>}
                            <Toggle /></div>
                            <ul style={{ listStyleType: "none", lineHeight: "45px", marginLeft: "2rem" }}>
                                <li>
                                    <Link activeClass="active" to="Intro" onClick={toggleVisibility} >
                                        Home
                                    </Link>
                                </li>
                                <li>
                                    <Link to="services" onClick={toggleVisibility}>
                                        Serivces
                                    </Link>
                                </li>
                                <li>
                                    <Link to="works" onClick={toggleVisibility}>
                                        Experience
                                    </Link>
                                </li>
                                <li>
                                    <Link to="portfolio" onClick={toggleVisibility}>
                                        Protfolio
                                    </Link>
                                </li>
                                <li>
                                    <Link to="testimonial" onClick={toggleVisibility}>
                                        Testimonial
                                    </Link>
                                </li>
                                <li>
                                    <Link to="contact" onClick={toggleVisibility}>
                                        Contact
                                    </Link>
                                </li>
                                <li>
                                    <a href="https://github.com/VikashSaini-RKD?tab=repositories"  onClick={toggleVisibility}>
                                        GitHub
                                    </a>
                                </li>
                                <li>
                                    <a href="https://www.linkedin.com/in/vikash-saini-771879290/" onClick={toggleVisibility}>
                                        LinkedIn
                                    </a>
                                </li>
                                <li>
                                    <Link to="services" onClick={toggleVisibility}>
                                        Download CV
                                    </Link>
                                </li>
                            </ul>
                        </div>
                    </div>
                ) : <div className='active remove'></div>}
            </div>
        </>

    );
}

export default Sidebar;
