import React ,{useContext,useEffect}from "react";
import { themeContext } from "../../Context";
import "./Testimonial.css";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import { Pagination } from "swiper";
import "swiper/css/pagination";
import profilePic1 from "../img/vs.jpg";
import profilePic2 from "../img/satyudev.jpg";
import profilePic3 from "../img/aniket.jpg";
import profilePic4 from "../img/vs.jpg";
import Aos from "aos";

const Testimonial = () => {
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;
  const clients = [
    {
      img: profilePic1,
      review:
        "I'm Vikash Saini Full Stack Developer with a passion for coding and programming. Skilled in both frontend and backend technology, include HTML, CSS, JAVASCRIPT, BOOTSTRAP, REACT JS, NODE JS, EXPRESS JS, MONGODB,(Basic SQL),(Basic C & C++)."
    },
    {
      img: profilePic2,
      review:
      "I'm Satyadev Saini Full Stack Developer with a passion for coding and programming. Skilled in both frontend and backend technology, include HTML, CSS, JAVASCRIPT, BOOTSTRAP, REACT JS, JAVA + DSA, JSE, JEE, Spring Boot, Spring Boot MVC, Spring Boot RESTFUL API, Spering Boot Security, Mysql."
    },
    {
      img: profilePic3,
      review:
      "I'm Vishal Saini Full Stack Developer with a passion for coding and programming. Skilled in both frontend and backend technology, include HTML, CSS, JAVASCRIPT, BOOTSTRAP, JAVA + DSA, JSE, JEE, Spring Boot, Spring Boot MVC, Spring Boot RESTFUL API, Spering Boot Security, Mysql."
    },
    {
      img: profilePic4,
      review:
        "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Ex officiis molestiae quod tempora laudantium, cumque error a nisi placeat quae exercitationem, maiores reiciendis! Eaque dicta minima, iure maiores dolorum sed.",
    },
  ];
  useEffect(() => {    
    Aos.init({duration:1200, offset:400});
  }, [])

  return (
    <div className="t-wrapper" id="testimonial" data-aos="fade-left">
      <div className="t-heading">
        {/* <span>Clients always get </span> */}
        <span>Exceptional Work </span>
        <span>from me...</span>
      <div className="blur t-blur1" style={{ background: "var(--purple)" }}></div>
      <div className="blur t-blur2" style={{ background: "skyblue" }}></div>

      </div>
      <Swiper
        // install Swiper modules
        modules={[Pagination]}
        slidesPerView={1}
        pagination={{ clickable: true }}
      >
        {clients.map((client, index) => {
          return (
            <SwiperSlide key={index} >
              <div className="testimonial" >
                <img src={client.img} alt="" />
                <span style={{ color: darkMode ? "white" : "gray" }}>{client.review}</span>
              </div>
            </SwiperSlide>
          );
        })}
      </Swiper>
    </div>
  );
};

export default Testimonial;