import React, { useContext } from "react";
import { themeContext } from "../../Context"
import Toggle from "../Toggle/Toggle";
import "./Navbar.css";
import { Link } from "react-scroll";

const Navbar = () => {
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;
  return (
    <>
    <div className="n-wrapper" id="navbars" style={{ backgroundColor:darkMode?"black":"white" }} >
      <div className="n-left">
        <Toggle />
      </div>
      <div className="n-right"  style={{ color: darkMode ? "white" : "Black"}}>
        <div className="n-list">
          <ul style={{ listStyleType: "none" }}>
            <li><Link activeClass="active" to="Intro" >Home</Link></li>
            <li><Link to="services" >Serivces</Link></li>
            <li><Link to="works">Experience</Link></li>
            <li><Link to="portfolio">Protfolio</Link></li>
            <li><Link to="testimonial">Testimonial</Link></li>
          </ul>
        </div>
      </div>
        <Link to="contact"><button className="button n-button ">Contact</button></Link>
    </div>
        <Link to="contact"><button className=" n-button ">Contact</button></Link>
        </>
  );
};

export default Navbar;