import React, { useContext, useEffect } from "react";
import "./Portfolio.css";
import { Swiper, SwiperSlide } from "swiper/react"
import "swiper/css";
import Sidebar from "../img/sidebar.png";
import Ecommerce from "../img/clone.jpg";
import HOC from "../img/portfolio.jpeg";
import MusicApp from "../img/ecommerce.png";
import ecommerce from "../img/mern.jpeg";
import { themeContext } from "../../Context";
import Aos from "aos";
import "aos/dist/aos.css";

const Portfolio = () => {
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;

  useEffect(() => {    
    Aos.init({duration:1200, offset:300});
  }, [])

  return (
    <div className="portfolio" id="portfolio" data-aos="fade-left">
      {/* heading */}
      <span style={{color: darkMode?'white': ''}}>Recent Projects </span>
      <span> Portfolio</span>

      {/* slider */}
      <Swiper
        spaceBetween={40}
        slidesPerView={3}
        grabCursor={true}
        className="portfolio-slider"
      >
        <SwiperSlide>
          <img src={Sidebar} alt="" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={Ecommerce} alt="" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={MusicApp} alt="" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={HOC} alt="" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={ecommerce} alt="" />
        </SwiperSlide>
      </Swiper>
    </div>
  );
};

export default Portfolio;